import React from 'react';
import { motion } from 'framer-motion';

import classes from './ContactPageList.module.scss';

const childVariants = {
  start: { y: '-6%' },
  end: {
    y: '6%',
    transition: {
      type: 'tween',
      duration: 1.8,
      ease: 'easeInOut',
      repeat: Infinity,
      repeatType: 'reverse',
    },
  },
};

const contactPageList = ({ LogoSvg, children }) => {
  childVariants.end.x = childVariants.start.x = `${
    Math.random() < 0.5 ? Math.random() * -10 : Math.random() * 10
  }%`;
  return (
    <div className={classes.ContactPageList}>
      <motion.div className={classes.LogoSvg} variants={childVariants}>
        {LogoSvg}
      </motion.div>
      <div className={classes.Text}>{children}</div>
    </div>
  );
};

export default contactPageList;
