import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { motion } from 'framer-motion';

import { H2, H2SubHeading } from '../components/Typography/Typography';
import Card from '../components/UI/Card/Card';
import Gmail from '../assets/icons/contact/gmail.inline.svg';
import Phone from '../assets/icons/contact/phone.inline.svg';
import Whatsapp from '../assets/icons/contact/whatsapp.inline.svg';
import ContactPageList from '../components/ContactPageList/ContactPageList';
import Head from '../components/Head/Head';

import classes from './contact.module.scss';

const containerVariants = {
  end: {
    transition: {
      staggerChildren: 1.4,
    },
  },
};

const Contact = () => {
  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          contactGmail
          contactPhones
          contactWhatsApp
        }
      }
    }
  `);

  return (
    <div className={classes.ContactPageBody}>
      <Head content="Contact information of Satadru Roy">
        contact me | Satadru Roy
      </Head>
      <div className={classes.Headings}>
        <H2>contact me</H2>
        <H2SubHeading>
          <span className={classes.H2Sub}>Get in touch with me now!</span>
        </H2SubHeading>
      </div>
      <Card>
        <motion.div
          className={classes.Body}
          variants={containerVariants}
          initial="start"
          animate="end"
        >
          <ContactPageList LogoSvg={<Gmail />}>
            {data.site.siteMetadata.contactGmail}
          </ContactPageList>
          <ContactPageList
            LogoSvg={
              <Phone style={{ transform: 'scale(1.3) rotateZ(10deg)' }} />
            }
          >
            {data.site.siteMetadata.contactPhones.join(' / ')}
          </ContactPageList>
          <ContactPageList LogoSvg={<Whatsapp />}>
            {data.site.siteMetadata.contactWhatsApp.join(' / ')}
          </ContactPageList>
        </motion.div>
      </Card>
    </div>
  );
};

export default Contact;
